.toolbar{position:fixed;right:0;bottom:0;z-index:99;width:100%;height:56px;padding:0 24px;line-height:56px;background:#fff;border-top:1px solid #e8e8e8;box-shadow:0 -1px 2px rgba(0,0,0,.03)}.toolbar:after{display:block;clear:both;content:""}.toolbar .left{float:left}.toolbar .right{float:right}.toolbar button+button{margin-left:8px}