.card{margin-bottom:24px}.heading{margin:0 0 16px;font-size:14px;line-height:22px}.steps:global(.ant-steps){max-width:750px;margin:16px auto}.errorIcon{margin-right:24px;color:#f5222d;cursor:pointer}.errorIcon i{margin-right:4px}.errorPopover :global .ant-popover-inner-content{min-width:256px;max-height:290px;padding:0;overflow:auto}.errorListItem{padding:8px 16px;list-style:none;border-bottom:1px solid #e8e8e8;cursor:pointer;transition:all .3s}.errorListItem:hover{background:#e6f7ff}.errorListItem:last-child{border:0}.errorListItem .errorIcon{float:left;margin-top:4px;margin-right:12px;padding-bottom:22px;color:#f5222d}.errorListItem .errorField{margin-top:2px;color:rgba(0,0,0,.45);font-size:12px}.editable td{padding-top:13px!important;padding-bottom:12.5px!important}.advancedForm+div{padding-bottom:64px}.advancedForm :global .ant-form .ant-row:last-child .ant-form-item{margin-bottom:24px}.advancedForm :global .ant-table td{transition:none!important}.optional{color:rgba(0,0,0,.45);font-style:normal}