.activeChart{position:relative}.activeChartGrid p{position:absolute;top:80px}.activeChartGrid p:last-child{top:115px}.activeChartLegend{position:relative;height:20px;margin-top:8px;font-size:0;line-height:20px}.activeChartLegend span{display:inline-block;width:33.33%;font-size:12px;text-align:center}.activeChartLegend span:first-child{text-align:left}.activeChartLegend span:last-child{text-align:right}.dashedLine{position:relative;top:-70px;left:-3px;height:1px}.dashedLine .line{position:absolute;top:0;left:0;width:100%;height:100%;background-image:linear-gradient(90deg,transparent 50%,#e9e9e9 0);background-size:6px}.dashedLine:last-child{top:-36px}