.avatarHolder{margin-bottom:24px;text-align:center}.avatarHolder>img{width:104px;height:104px;margin-bottom:20px}.avatarHolder .name{margin-bottom:4px;color:rgba(0,0,0,.85);font-weight:500;font-size:20px;line-height:28px}.detail p{position:relative;margin-bottom:8px;padding-left:26px}.detail p:last-child{margin-bottom:0}.detail i{position:absolute;top:4px;left:0;width:14px;height:14px;background:url(https://gw.alipayobjects.com/zos/rmsportal/pBjWzVAHnOOtAUvZmZfy.svg)}.detail i.title{background-position:0 0}.detail i.group{background-position:0 -22px}.detail i.address{background-position:0 -44px}.tagsTitle,.teamTitle{margin-bottom:12px;color:rgba(0,0,0,.85);font-weight:500}.tags :global .ant-tag{margin-bottom:8px}.team :global .ant-avatar{margin-right:12px}.team a{display:block;margin-bottom:24px;overflow:hidden;color:rgba(0,0,0,.65);white-space:nowrap;text-overflow:ellipsis;word-break:break-all;transition:color .3s}.team a:hover{color:#1890ff}.tabsCard :global .ant-card-head{padding:0 16px}