.tagSelect{position:relative;max-height:32px;margin-left:-8px;overflow:hidden;line-height:32px;transition:all .3s;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.tagSelect :global .ant-tag{margin-right:24px;padding:0 8px;font-size:14px}.tagSelect.expanded{max-height:200px;transition:all .3s}.tagSelect .trigger{position:absolute;top:0;right:0}.tagSelect .trigger i{font-size:12px}.tagSelect.hasExpandTag{padding-right:50px}