.list{max-height:400px;overflow:auto}.list::-webkit-scrollbar{display:none}.list .item{padding-right:24px;padding-left:24px;overflow:hidden;cursor:pointer;transition:all .3s}.list .item .meta{width:100%}.list .item .avatar{margin-top:4px;background:#fff}.list .item .iconElement{font-size:32px}.list .item.read{opacity:.4}.list .item:last-child{border-bottom:0}.list .item:hover{background:#e6f7ff}.list .item .title{margin-bottom:8px;font-weight:400}.list .item .description{font-size:12px;line-height:1.5}.list .item .datetime{margin-top:4px;font-size:12px;line-height:1.5}.list .item .extra{float:right;margin-top:-1.5px;margin-right:0;color:rgba(0,0,0,.45);font-weight:400}.list .loadMore{padding:8px 0;color:#1890ff;text-align:center;cursor:pointer}.list .loadMore.loadedAll{color:rgba(0,0,0,.25);cursor:unset}.notFound{padding:73px 0 88px;color:rgba(0,0,0,.45);text-align:center}.notFound img{display:inline-block;height:76px;margin-bottom:16px}.bottomBar{height:46px;color:rgba(0,0,0,.65);line-height:46px;text-align:center;border-top:1px solid #e8e8e8;border-radius:0 0 4px 4px;transition:all .3s}.bottomBar div{display:inline-block;width:50%;cursor:pointer;transition:all .3s;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.bottomBar div:hover{color:rgba(0,0,0,.85)}.bottomBar div:only-child{width:100%}.bottomBar div:not(:only-child):last-child{border-left:1px solid #e8e8e8}